export default {
  data(){
    return {
      indexArr: [0, 1, 2],
      index: 1
    }
  },

  methods: {
    styleColor(index){
      return `color:${this.index === index ? '#00B9EF' : ''}`
    },
    selectItem(el){
      let currentIndex = Number(el.currentTarget.dataset.index);
      this.index = currentIndex;
      this.$emit('switchItem', currentIndex);
    }
  }
}



